export default{
	components:{
		tagSexAge
	},
	props:{
		item:Object
	},
	data() {
		return {
			isguanzhu: this.item.isguanzhu
		}
	},
	methods:{
		// 关注
		guanzhu(){
			this.isguanzhu=true;
			uni.showToast({
				title:"关注成功"
			})
		},
		detailimg(index){
			// 预览图片
